<template>
  <div class="d-flex flex-column align-items-center">
    <br />
    <h1 class="my-5">Te damos la Bienvenida üëã</h1>

    <form
      class="d-flex flex-column"
      @submit.prevent="agregarUsuario()"
      enctype="multipart/form-data"
    >
      <div id="form" class="container px-5 pb-3">
        <h3 class="mt-3">Nuevo usuario</h3>
        <input
          type="text"
          class="form-control my-2"
          placeholder="Nombre"
          v-model="usuario.nombre"
          required
        />

        <div class="d-flex">
          <label class="" for="date">Fecha de nacimiento:</label>
          <input
            type="date"
            id="date"
            class="form-control my-2"
            v-model="usuario.fechaNacimiento"
            required
          />
        </div>
        <div class="d-flex align-items-center">
          Foto de perfil:
          <input
            id="fotoPerfil"
            type="file"
            class="form-control m-2"
            @change="agregarFile"
          />
        </div>
        <div class="input-group mb-2">
          <div class="input-group-prepend">
            <div class="input-group-text my-2">@</div>
          </div>
          <input
            type="text"
            class="form-control my-2"
            id="inlineFormInputGroup"
            placeholder="Username"
            v-model="usuario.usuario"
            required
          />
        </div>
        <div>
          <b-form  @submit.stop.prevent>
            <b-form-input v-model="usuario.password" 
              type="password" placeholder="Contrase√±a" 
              :state="validation" id="feedback-user">
            </b-form-input>
            <b-form-invalid-feedback :state="validation">
              Debe tener entre 8 y 16 caracteres, al menos un d√≠gito, al menos una min√∫scula y al menos una may√∫scula.
            </b-form-invalid-feedback>
            <b-form-valid-feedback :state="validation">
              ok.
            </b-form-valid-feedback>
          </b-form>
        </div>
      </div>

      <b-button id="enviar" pill class="my-4" type="submit">Enviar</b-button>
    </form>

    <div class="container">
      <b-alert
        :show="dismissCountDown"
        :variant="mensaje.color"
        @dismissed="dismissCountDown = 0"
        @dismiss-count-down="countDownChanged"
      >
        <p>{{ mensaje.texto }}</p>
        <b-progress
          :variant="mensaje.color"
          :max="dismissSecs"
          :value="dismissCountDown"
          height="4px"
        ></b-progress>
      </b-alert>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      usuarios: [],
      dismissSecs: 5,
      dismissCountDown: 0,
      showDismissibleAlert: false,
      mensaje: {
        color: '',
        texto: '',
      },
      usuario: {
        nombre: '',
        tatuador: Boolean,
        fechaNacimiento: null,
        fotoPerfil: '',
        usuario: '',
        password: '',
        aboutYou: '',
      },
      valida: ''
    }
  },
  beforeCreate() {
    if (localStorage.getItem('id') != null) {
      this.$router.go(-1)
    }
  },
  computed: {
     validation() {
         var regexp = /^(?=\w*\d)(?=\w*[A-Z])(?=\w*[a-z])\S{8,16}$/
        if (regexp.test(this.usuario.password)) {
          this.valida = true
        }else{
          this.valida = false
        }
        return regexp.test(this.usuario.password)
      }
    },
  methods: {
    alertaTrue() {
      this.mensaje.color = 'success'
      this.mensaje.texto = 'Usuario agregado con exito! üëå'
      this.showAlert()
    },
    alertaFalse() {
      this.mensaje.color = 'warning'
      this.mensaje.texto = 'Error :('
      this.showAlert()
    },
    alertaUsuarioExistente() {
      alert('Nombre de usuario ya existente')
    },
    agregarUsuario() {
      this.axios
        .post('/usuarios/comprobarUsuario', this.usuario)
        .then((res) => {
          if (res.data.existe == false) {
            if (this.valida == true) {
              const formdata = new FormData()
              formdata.append('fotoPerfil', this.usuario.fotoPerfil)
              this.axios
                .post('/usuarios/agregarFoto', formdata)
                .then((res) => {
                  this.usuarios.push(res.data)
                  this.usuario.fotoPerfil = res.data
                  this.alertaTrue()
                })
                .catch((e) => {
                  this.alertaFalse()
                })
                .finally((e) => {
                  this.axios
                    .post('/usuarios/agregar', this.usuario)
                    .then((res) => {
                      this.usuarios.push(res.data)
                      this.alertaTrue()
                    })
                    .catch((e) => {
                      this.alertaFalse()
                    })
                    .finally((e) => {
                      this.usuario.nombre = ''
                      this.usuario.tatuador = false
                      this.usuario.fotoPerfil = ''
                      this.usuario.fechaNacimiento = new Date()
                      this.usuario.usuario = ''
                      this.usuario.password = ''
                    })
                })
            }else{
              this.alertaFalse
            }
          } else {
            this.alertaUsuarioExistente()
          }
        })
    },
    agregarFile(e) {
      var file = e.target.files[0]
      const types = ['image/jpg', 'image/jpeg', 'image/png']
      if (types.includes(file.type)) {
        this.usuario.fotoPerfil = file
      }
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
      if (this.mensaje.color == 'success') {
        setTimeout(() => {
          window.location.href = '/'
        }, 5000)
      }
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    },
  },
}
</script>

<style>
#form {
  background-color: #82a5c7;
  color: #243241;
  border-radius: 10px;
}
input {
  background-color: #4e647a !important;
  color: #82a5c7 !important;
}
#enviar {
  background-color: #82a5c7;
  color: #243241;
}
</style>
